.button {
    width: 130px;
    height: 30px;
    color: white;
    border: none;
    border-radius: 3px;
}


.disabled {
    background: rgba(0, 46, 92, 1);
    opacity: 0.4;
    color: #5b4c4c;
}

.secondary {
    border: 1px solid rgba(0, 102, 204, 1);
    background-color: white;
    color: rgba(0, 102, 204, 1);
    width: 130px;
    height: 30px;
    font-size: 13px;
    font-weight: bold;

}

.default {
    background: rgba(0, 102, 204, 1);
    font-size: 13px;
    font-weight: bold;

}

.red {
    background: rgba(204, 20, 57, 1);

